//接口文件  index.js
import { request } from './core'
import { METHODS, PATH } from './constants'
const APIclient = {
    login(params) {//登录
        return request(METHODS.POST, PATH.LOGIN, params)
    },
    send(params) {//发送验证码
        return request(METHODS.POST, PATH.SEND, params)
    },
    reg(params) {//注册	
        return request(METHODS.POST, PATH.REG, params)
    },
    isph(params) {//验证手机号是否存在
        return request(METHODS.POST, PATH.ISPH, params)
    },
    logout(params) {//退出登录
        return request(METHODS.POST, PATH.LOGOUT, params)
    },
    classlist(params) {//供货商分类列表
        return request(METHODS.POST, PATH.CLASSLIST, params)
    },
    addClass(params) {//供货商分类	
        return request(METHODS.POST, PATH.ADDCLASS, params)
    },
    getmer(params) {//供货商详情
        return request(METHODS.POST, PATH.GETMER, params)
    },
    merpic(params) {//供货商图片列表
        return request(METHODS.POST, PATH.MERPIC, params)
    },
    addMerInfo(params) {//新增供货商详情信息
        return request(METHODS.POST, PATH.ADDMERINFO, params)
    },
    list(params) {//商品列表
        return request(METHODS.POST, PATH.LSIT, params)
    },
    add(params) {//商品新增
        return request(METHODS.POST, PATH.ADD, params)
    },
    update(params) {//修改密码
        return request(METHODS.POST, PATH.UPDATE, params)
    },
    forgo(params) {//忘记密码	
        return request(METHODS.POST, PATH.FORGO, params)
    },
    meraddress(params) {//配置管理列表	
        return request(METHODS.GET, PATH.MERADDRESS, { params })
    },
    edit(params) {//商品编辑
        return request(METHODS.POST, PATH.EDIT, params)
    },
    details(params) {//商品详情
        return request(METHODS.POST, PATH.DETAILS, params)
    },
    piclist(params) {//商品图片详情列表
        return request(METHODS.POST, PATH.PICLIST, params)
    },
    contract(params) {//合同列表
        return request(METHODS.GET, PATH.CONTRACTLIST, params)
    },
    sign(params) {//合同签署
        return request(METHODS.POST, PATH.CONTRACTSIGN, params)
    },
    download(params) {//合同下载
        return request(METHODS.POST, PATH.DOWNLOAD, params)
    },
    addmodlfy(params) {//添加退换货地址	
        return request(METHODS.POST, PATH.ADDMODIFY, params)
    },
    seallist(params) {//合同印章列表
        return request(METHODS.GET, PATH.SEALLIST, params)
    },
    sealup(params) {//合同印章上传
        return request(METHODS.POST, PATH.SEALUP, params)
    },
    sealdel(params) {//合同印章删除
        return request(METHODS.POST, PATH.SEALDEL, params)
    },
    cancel(params) {//取消订单
        return request(METHODS.POST, PATH.CANCEL, params)
    },
    delivery(params) {//发货
        return request(METHODS.POST, PATH.DELIVERY, params)
    },
    meruoload(params) {//上传图片
        return request(METHODS.POST, PATH.MERUOLOAD, params)
    },
    uploadimg(params) {//上传商品图片
        return request(METHODS.POST, PATH.UPLOADIMG, params)
    },
    merdelete(params) {//删除图片
        return request(METHODS.POST, PATH.MERDELETE, params)
    },
    addmerInfo(params) {//新增供货商详情
        return request(METHODS.POST, PATH.ADDMERINFO, params)
    },
    shelves(params) {//商品上架
        return request(METHODS.POST, PATH.SHELVES, params)
    },
    putdown(params) {//商品下架
        return request(METHODS.POST, PATH.PUTDOWN, params)
    },
}
export default APIclient