<template>
  <div class="login-body">
    <div class="login-box">
      <div class="login-bg">
        <div class="login-box-center">
          <div class="login-top">
            <div>
              <img src="/logo.png" alt="" />
            </div>
          </div>
          <div class="login-section">
            <a-input v-model="username" type="text" placeholder="请输入账号" />
            <a-input
              v-model="password"
              type="password"
              placeholder="请输入密码"
              @keyup.enter.native="login"
            />
          </div>
          <div class="login-footer">
            <h3 @click="onclickregister">注册</h3>
            <h3 @click="onclickforget">忘记密码</h3>
          </div>

          <a-button type="primary" size="large" block @click="login"
            >登录</a-button
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      password: "",
    };
  },
  methods: {
    onclickregister() {
      this.$router.push({
        path: "/register",
      });
    },
    onclickforget() {
      this.$router.push({
        path: "/forget",
      });
    },
    async login() {
      if (!this.username == " " && !this.password == "") {
        let str = {
          phone: this.username,
          password: this.$md5(this.password),
        };
        let { data } = await this.$http.login(str);
        console.log(data);
        if (data.code == "0000") {
          sessionStorage.setItem("token", data.token);
          sessionStorage.setItem("id", data.mer.id);
          if (data.mer.merInfo == null) {
            this.$router.push({
              path: "/explain",
            });
          } else {
            this.$message({
              showClose: true,
              message: "登录成功",
              type: "success",
            });
            this.$router.replace({
              path: "/home",
            });
          }
        } else {
          this.$message({
            showClose: true,
            message: data.message,
          });
          this.username = "";
          this.password = "";
        }
      } else {
        this.$message({
          showClose: true,
          message: "账号或密码为空",
          type: "error",
        });
      }
    },
  },
};
</script>


